import { IndexedDBService } from './indexeddb';
import { NeonDBService } from './neon';
import { SyncService } from './sync';
import { DatabaseInitializer } from './init';

export class DatabaseService {
  static async initialize() {
    try {
      // Initialize IndexedDB
      await IndexedDBService.initialize();
      
      // Check and create Neon DB tables if they don't exist
      if (process.env.NEXT_PUBLIC_NEON_DATABASE_URL) {
        try {
          await DatabaseInitializer.checkAndCreateTables();
        } catch (error) {
          console.warn('Neon database setup failed, continuing with offline mode only:', error.message);
        }
      } else {
        console.log('Neon database not configured, running in offline mode only');
      }
      
      // Initialize sync service
      await SyncService.initialize();
      
      console.log('Database service initialized successfully');
    } catch (error) {
      console.error('Error initializing database service:', error);
      throw error;
    }
  }

  // User operations with dual database support
  static async createUser(userData) {
    try {
      // Always create in IndexedDB first (offline-first approach)
      const localUser = await IndexedDBService.createUser(userData);
      
      // If online and Neon DB is available, also create in Neon DB
      if (IndexedDBService.isOnline() && NeonDBService.isAvailable()) {
        try {
          await NeonDBService.createUser(userData);
          // Update local user to mark as online
          await IndexedDBService.updateUser(localUser.id, { isOnline: true });
        } catch (error) {
          console.error('Error creating user in Neon DB:', error);
          // Add to sync queue for later
          await IndexedDBService.addToSyncQueue('users', localUser.id, 'create', userData);
        }
      } else {
        // Add to sync queue for when online
        await IndexedDBService.addToSyncQueue('users', localUser.id, 'create', userData);
      }
      
      return localUser;
    } catch (error) {
      console.error('Error creating user:', error);
      throw error;
    }
  }

  static async getUserByEmail(email) {
    try {
      // Try to get from IndexedDB first
      let user = await IndexedDBService.getUserByEmail(email);
      
      // If not found locally and online, try Neon DB
      if (!user && IndexedDBService.isOnline()) {
        try {
          user = await NeonDBService.getUserByEmail(email);
          if (user) {
            // Store in IndexedDB for offline access
            await IndexedDBService.createUser(user);
          }
        } catch (error) {
          console.error('Error getting user from Neon DB:', error);
        }
      }
      
      return user;
    } catch (error) {
      console.error('Error getting user by email:', error);
      throw error;
    }
  }

  static async getUserById(id) {
    try {
      // Try to get from IndexedDB first
      let user = await IndexedDBService.getUserById(id);
      
      // If not found locally and online, try Neon DB
      if (!user && IndexedDBService.isOnline()) {
        try {
          user = await NeonDBService.getUserById(id);
          if (user) {
            // Store in IndexedDB for offline access
            await IndexedDBService.createUser(user);
          }
        } catch (error) {
          console.error('Error getting user from Neon DB:', error);
        }
      }
      
      return user;
    } catch (error) {
      console.error('Error getting user by ID:', error);
      throw error;
    }
  }

  static async updateUser(id, userData) {
    try {
      // Update in IndexedDB first
      const localUser = await IndexedDBService.updateUser(id, userData);
      
      // If online, also update in Neon DB
      if (IndexedDBService.isOnline()) {
        try {
          await NeonDBService.updateUser(id, userData);
        } catch (error) {
          console.error('Error updating user in Neon DB:', error);
          // Add to sync queue for later
          await IndexedDBService.addToSyncQueue('users', id, 'update', userData);
        }
      } else {
        // Add to sync queue for when online
        await IndexedDBService.addToSyncQueue('users', id, 'update', userData);
      }
      
      return localUser;
    } catch (error) {
      console.error('Error updating user:', error);
      throw error;
    }
  }

  // Patient operations with dual database support
  static async createPatient(patientData) {
    try {
      // Always create in IndexedDB first (offline-first approach)
      const localPatient = await IndexedDBService.createPatient(patientData);
      
      // If online, also create in Neon DB
      if (IndexedDBService.isOnline()) {
        try {
          await NeonDBService.createPatient(patientData);
          // Update local patient to mark as online
          await IndexedDBService.updatePatient(localPatient.id, { isOnline: true });
        } catch (error) {
          console.error('Error creating patient in Neon DB:', error);
          // Add to sync queue for later
          await IndexedDBService.addToSyncQueue('patients', localPatient.id, 'create', patientData);
        }
      } else {
        // Add to sync queue for when online
        await IndexedDBService.addToSyncQueue('patients', localPatient.id, 'create', patientData);
      }
      
      return localPatient;
    } catch (error) {
      console.error('Error creating patient:', error);
      throw error;
    }
  }

  static async getPatients() {
    try {
      // Get from IndexedDB first
      let patients = await IndexedDBService.getPatients();
      
      // If online, also sync from Neon DB
      if (IndexedDBService.isOnline()) {
        try {
          const onlinePatients = await NeonDBService.getPatients();
          // Merge online patients with local ones
          for (const onlinePatient of onlinePatients) {
            const localPatient = patients.find(p => p.id === onlinePatient.id);
            if (!localPatient) {
              // Add new patient from online
              await IndexedDBService.createPatient(onlinePatient);
              patients.push(onlinePatient);
            }
          }
        } catch (error) {
          console.error('Error getting patients from Neon DB:', error);
        }
      }
      
      return patients;
    } catch (error) {
      console.error('Error getting patients:', error);
      throw error;
    }
  }

  static async getPatientById(id) {
    try {
      // Try to get from IndexedDB first
      let patient = await IndexedDBService.getPatientById(id);
      
      // If not found locally and online, try Neon DB
      if (!patient && IndexedDBService.isOnline()) {
        try {
          patient = await NeonDBService.getPatientById(id);
          if (patient) {
            // Store in IndexedDB for offline access
            await IndexedDBService.createPatient(patient);
          }
        } catch (error) {
          console.error('Error getting patient from Neon DB:', error);
        }
      }
      
      return patient;
    } catch (error) {
      console.error('Error getting patient by ID:', error);
      throw error;
    }
  }

  static async updatePatient(id, patientData) {
    try {
      // Update in IndexedDB first
      const localPatient = await IndexedDBService.updatePatient(id, patientData);
      
      // If online, also update in Neon DB
      if (IndexedDBService.isOnline()) {
        try {
          await NeonDBService.updatePatient(id, patientData);
        } catch (error) {
          console.error('Error updating patient in Neon DB:', error);
          // Add to sync queue for later
          await IndexedDBService.addToSyncQueue('patients', id, 'update', patientData);
        }
      } else {
        // Add to sync queue for when online
        await IndexedDBService.addToSyncQueue('patients', id, 'update', patientData);
      }
      
      return localPatient;
    } catch (error) {
      console.error('Error updating patient:', error);
      throw error;
    }
  }

  static async deletePatient(id) {
    try {
      // Delete from IndexedDB first
      await IndexedDBService.deletePatient(id);
      
      // If online, also delete from Neon DB
      if (IndexedDBService.isOnline()) {
        try {
          await NeonDBService.deletePatient(id);
        } catch (error) {
          console.error('Error deleting patient from Neon DB:', error);
          // Add to sync queue for later
          await IndexedDBService.addToSyncQueue('patients', id, 'delete', {});
        }
      } else {
        // Add to sync queue for when online
        await IndexedDBService.addToSyncQueue('patients', id, 'delete', {});
      }
      
      return true;
    } catch (error) {
      console.error('Error deleting patient:', error);
      throw error;
    }
  }

  // Sync operations
  static async sync() {
    await SyncService.syncToOnline();
  }

  static async isOnline() {
    return IndexedDBService.isOnline();
  }
}

export default DatabaseService;
