'use client';

import { useEffect, useState } from 'react';
import { DatabaseService } from '@/lib/database';
import '@/lib/database/debug';

export default function DatabaseInitializer({ children }) {
  const [isInitialized, setIsInitialized] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    const initializeDatabase = async () => {
      try {
        await DatabaseService.initialize();
        setIsInitialized(true);
      } catch (error) {
        console.error('Error initializing database:', error);
        setError(error.message);
        // Still set as initialized to prevent blocking the app
        setIsInitialized(true);
      }
    };

    initializeDatabase();
  }, []);

  if (!isInitialized) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950">
        <div className="text-center">
          <div className="w-16 h-16 mx-auto mb-4 bg-gradient-to-br from-cyan-400 to-teal-500 rounded-3xl flex items-center justify-center shadow-2xl animate-pulse">
            <svg className="w-8 h-8 text-white" fill="currentColor" viewBox="0 0 24 24">
              <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"/>
            </svg>
          </div>
          <h2 className="text-2xl font-bold text-white mb-2">Initializing Database</h2>
          <p className="text-slate-400">Setting up offline and online storage...</p>
        </div>
      </div>
    );
  }

  if (error) {
    console.warn('Database initialization warning:', error);
  }

  return children;
}
